
"use client";

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import ProtectedRoute from '@/components/ProtectedRoute';
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Skeleton } from "@/components/ui/skeleton";
import { Briefcase, Clock, CheckCircle, XCircle } from "lucide-react";
import Link from 'next/link';

export default function StudentApplicationsPage() {
    const { user } = useAuth();
    const [applications, setApplications] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchApps = async () => {
            if (!user) return;
            try {
                const token = await user.getIdToken();
                const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api'}/opportunities/my-applications`, {
                    headers: { 'Authorization': `Bearer ${token}` }
                });

                if (!response.ok) throw new Error('Failed to fetch applications');

                const data = await response.json();
                setApplications(data);
            } catch (error) {
                console.error(error);
            } finally {
                setLoading(false);
            }
        };

        fetchApps();
    }, [user]);

    const getStatusBadge = (status: string) => {
        switch (status) {
            case 'Submitted': return <Badge variant="secondary" className="bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-100"><Clock className="w-3 h-3 mr-1" /> Submitted</Badge>;
            case 'Shortlisted': return <Badge variant="secondary" className="bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-100"><CheckCircle className="w-3 h-3 mr-1" /> Shortlisted</Badge>;
            case 'Accepted': return <Badge variant="default" className="bg-green-600"><CheckCircle className="w-3 h-3 mr-1" /> Accepted</Badge>;
            case 'Rejected': return <Badge variant="destructive"><XCircle className="w-3 h-3 mr-1" /> Rejected</Badge>;
            default: return <Badge variant="outline">{status}</Badge>;
        }
    };

    return (
        <ProtectedRoute allowedRoles={['student']}>
            <div className="max-w-4xl mx-auto py-10 space-y-8 animate-in fade-in-50">
                <div className="flex justify-between items-center">
                    <div>
                        <h1 className="text-3xl font-bold tracking-tight">My Applications</h1>
                        <p className="text-muted-foreground">Track the status of your internship and research applications.</p>
                    </div>
                    <Link href="/opportunities">
                        <Button>Browse More</Button>
                    </Link>
                </div>

                {loading ? (
                    <div className="space-y-4">
                        {[1, 2].map(i => <Skeleton key={i} className="h-24 w-full" />)}
                    </div>
                ) : applications.length === 0 ? (
                    <Card className="text-center p-12 border-dashed">
                        <div className="flex flex-col items-center justify-center text-muted-foreground">
                            <div className="w-12 h-12 bg-muted rounded-full flex items-center justify-center mb-4">
                                <Briefcase className="w-6 h-6" />
                            </div>
                            <h3 className="text-lg font-medium text-foreground">No applications yet</h3>
                            <p className="mb-4">Start applying to opportunities to see them here.</p>
                            <Link href="/opportunities">
                                <Button variant="outline">Browse Opportunities</Button>
                            </Link>
                        </div>
                    </Card>
                ) : (
                    <div className="grid gap-4">
                        {applications.map((app: any) => (
                            <Card key={app.applicationId} className="hover:border-primary/50 transition-colors">
                                <CardHeader className="pb-2">
                                    <div className="flex justify-between items-start">
                                        <div>
                                            <CardTitle className="text-lg font-bold">{app.opportunityTitle}</CardTitle>
                                            <p className="text-sm text-muted-foreground">Applied on: {new Date(app.appliedAt?._seconds * 1000).toLocaleDateString()}</p>
                                        </div>
                                        {getStatusBadge(app.status)}
                                    </div>
                                </CardHeader>
                                <CardContent>
                                    <div className="bg-muted/30 p-3 rounded-md text-sm">
                                        <p className="font-medium mb-1">Cover Letter Note:</p>
                                        <p className="text-muted-foreground line-clamp-2">{app.coverLetter || 'No cover letter submitted.'}</p>
                                    </div>
                                </CardContent>
                            </Card>
                        ))}
                    </div>
                )}
            </div>
        </ProtectedRoute>
    );
}
